@import 'variables';
@import 'mixins';

.header {
  @include flexCenterBet;
  @include wh(100%, 150px);

  padding: 0 100px;

  &__block {
    display: flex;
    justify-content: space-between;
    min-width: 550px;
  }

  &-logo {
    max-width: 170px;
  }

  &__links {
    display: flex;
    align-items: center;
  }

  &__link {
    position: relative;
    margin-right: 45px;
    font-weight: bold;
    font-size: 18px;
    color: $dark;

    &:after {
      content: "";
      position: absolute;
      display: block;
      width: 110%;
      left: -5%;
      bottom: -1px;
      z-index: -1;
      height: 8px;
      background: $brightGreen;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}
